<template>
  <view class="pet-home">
    <view class="banner no-img">
      <text class="banner-title">宠物之家</text>
    </view>
    <view class="welcome">
      <text class="main-title">宠物之家</text>
      <text class="sub-title">欢迎来到你的宠物乐园</text>
    </view>
    <view class="pet-list-title">热门宠物</view>
    <scroll-view
      class="pet-list"
      scroll-x>
      <view
        class="pet-card"
        v-for="pet in pets"
        :key="pet.id"
        :style="{ background: pet.bg }">
        <view class="pet-avatar">
          <text class="pet-emoji">{{ pet.emoji }}</text>
        </view>
        <view class="pet-info">
          <text class="pet-name">{{ pet.name }}</text>
          <text class="pet-type">{{ pet.type }}</text>
        </view>
      </view>
    </scroll-view>
    <view class="tab-bar">
      <view
        class="tab-item"
        v-for="tab in tabs"
        :key="tab.name"
        :class="{ active: currentTab === tab.name }"
        @click="switchTab(tab.name)">
        <text class="tab-emoji">{{ tab.emoji }}</text>
        <text class="tab-text">{{ tab.label }}</text>
      </view>
    </view>
    <view class="tab-content">
      <view v-if="currentTab === 'baike'">
        <text>这里是宠物百科内容区，可展示宠物知识、养护技巧等。</text>
      </view>
      <view v-else-if="currentTab === 'health'">
        <text>这里是健康咨询内容区，可展示健康问答、常见疾病等。</text>
      </view>
      <view v-else-if="currentTab === 'adopt'">
        <text>这里是领养中心内容区，可展示待领养宠物信息。</text>
      </view>
      <view v-else-if="currentTab === 'mypet'">
        <text>这里是我的宠物内容区，可展示用户已添加的宠物。</text>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      pets: [
        { id: 1, name: '小橘', type: '猫咪', emoji: '🐱', bg: '#ffe4e1' },
        { id: 2, name: '旺财', type: '狗狗', emoji: '🐶', bg: '#e0f7fa' },
        { id: 3, name: '雪球', type: '兔子', emoji: '🐰', bg: '#fffde7' },
        { id: 4, name: '小绿', type: '鹦鹉', emoji: '🦜', bg: '#e8f5e9' },
      ],
      tabs: [
        { name: 'baike', label: '宠物百科', emoji: '📚' },
        { name: 'health', label: '健康咨询', emoji: '🩺' },
        { name: 'adopt', label: '领养中心', emoji: '🏠' },
        { name: 'mypet', label: '我的宠物', emoji: '🐾' },
      ],
      currentTab: 'baike',
    };
  },
  methods: {},
};
</script>

<style>
.pet-home {
  background: #f8f9fa;
  min-height: 100vh;
  padding-bottom: 120rpx;
}
.banner.no-img {
  width: 100vw;
  height: 320rpx;
  background: linear-gradient(90deg, #ffecd2 0%, #fcb69f 100%);
  display: flex;
  align-items: center;
  justify-content: center;
}
.banner-title {
  font-size: 56rpx;
  font-weight: bold;
  color: #fff;
  letter-spacing: 8rpx;
  text-shadow: 2rpx 4rpx 12rpx #fcb69f;
}
.welcome {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30rpx;
}
.main-title {
  font-size: 48rpx;
  font-weight: bold;
  color: #333;
}
.sub-title {
  font-size: 28rpx;
  color: #888;
  margin-top: 10rpx;
}
.pet-list-title {
  font-size: 32rpx;
  font-weight: bold;
  margin: 40rpx 0 20rpx 30rpx;
  color: #444;
}
.pet-list {
  display: flex;
  flex-direction: row;
  padding-left: 30rpx;
  margin-bottom: 30rpx;
}
.pet-card {
  width: 180rpx;
  margin-right: 24rpx;
  border-radius: 18rpx;
  box-shadow: 0 2rpx 8rpx #eee;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: box-shadow 0.2s;
}
.pet-card:active {
  box-shadow: 0 4rpx 16rpx #ccc;
}
.pet-avatar {
  width: 120rpx;
  height: 120rpx;
  border-radius: 60rpx;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20rpx;
  margin-bottom: 10rpx;
  font-size: 64rpx;
  box-shadow: 0 2rpx 8rpx #f0f0f0;
}
.pet-emoji {
  font-size: 64rpx;
}
.pet-info {
  padding: 12rpx 0 20rpx 0;
  text-align: center;
}
.pet-name {
  font-size: 28rpx;
  color: #222;
  font-weight: bold;
}
.pet-type {
  font-size: 22rpx;
  color: #888;
}
.tab-bar {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  box-shadow: 0 -2rpx 8rpx #eee;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 100rpx;
  z-index: 10;
}
.tab-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
  padding: 10rpx 0;
  color: #888;
  font-size: 24rpx;
  transition: color 0.2s;
}
.tab-item.active {
  color: #ff8a65;
  font-weight: bold;
}
.tab-emoji {
  font-size: 44rpx;
  margin-bottom: 4rpx;
}
.tab-content {
  margin: 40rpx 30rpx 120rpx 30rpx;
  min-height: 200rpx;
  background: #fff;
  border-radius: 18rpx;
  box-shadow: 0 2rpx 8rpx #eee;
  padding: 30rpx;
  font-size: 28rpx;
  color: #444;
}
</style>
